import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <React.Fragment>
      <header id="home">
        <nav id="nav-wrap">
          <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
          <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
          <ul id="nav" className="nav">
            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
            <li><a className="smoothscroll" href="#about">About</a></li>
            <li><a className="smoothscroll" href="#resume">Education</a></li>
            <li><a className="smoothscroll" href="#portfolio">Travel</a></li>
            <li><a className="smoothscroll" href="#testimonials">Blogs</a></li>
            <li><a className="smoothscroll" href="#contact">Contact</a></li>
          </ul> {/* end #nav */}
        </nav> {/* end #nav-wrap */}
        <div className="row banner">
          <div className="banner-text">
            <h1 className="responsive-headline">I'm {resumeData.name}</h1>
            <br/>
            <br/>
            <h3><span>{resumeData.role}, Illustrator and Web ethusiast doing creative and effective work for college projects. Let's <a className="smoothscroll" href="#about">start scrolling </a> 
              and learn more <a className="smoothscroll" href="#about">about me</a>.</span></h3>
            <hr />
            <ul className="social">
              <li><a href="https://www.facebook.com/profile.php?id=100004630487639"><i className="fa fa-facebook" /></a></li>
              <li><a href="https://twitter.com/Shivana94163798"><i className="fa fa-twitter" /></a></li>
              <li><a href="https://www.linkedin.com/in/shivanand-kamble"><i className="fa fa-linkedin" /></a></li>
              <li><a href="https://www.instagram.com/shivanand_55/"><i className="fa fa-instagram" /></a></li>
            </ul>
          </div>
        </div>
        <p className="scrolldown">
          <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
        </p>
      </header>
      </React.Fragment>
    );
  }
}