import React, { Component } from 'react';
export default class Portfolio extends Component {
  render() {
    return (
      <React.Fragment>
      <section id="portfolio">
        <div className="row">
          <div className="twelve columns collapsed">
            <h1>Check Out Some of Travel History of Mine.</h1>
            {/* portfolio-wrapper */}
            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
              <div className="columns portfolio-item">
                <div className="item-wrap">
                    <img src="images/portfolio/ooty.jpg"/>
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Ooty</h5>
                        <p>Queen of hils</p>
                      </div>  
                    </div>
  
                </div>
              </div> {/* item end */}
              <div className="columns portfolio-item">
                <div className="item-wrap">
                    <img src="images/portfolio/imagica.jpg" />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Imagica</h5>
                        <p>Imagicaa Amusement Theme Park</p>
                      </div>
                    </div>
                  
                </div>
              </div> {/* item end */}
              <div className="columns portfolio-item">
                <div className="item-wrap">
                    <img alt="" src="images/portfolio/mysore.jpg" />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Mysore</h5>
                        <p>  Mysore is also home to the centuries-old Devaraja Market, filled with spices, silk and sandalwood.</p>
                      </div>
                    </div>
                </div>
              </div> {/* item end */}
              <div className="columns portfolio-item">
                <div className="item-wrap">
                    <img alt="" src="images/portfolio/ajinkyatara.jpg" />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Ajinkyatara</h5>
                        <p>Ajinkyatara is a fort on one of the seven mountains surrounding the city of Satara in the Sahayadri Mountains</p>
                      </div>
                    </div>
                
                </div>
              </div> {/* item end */}
              <div className="columns portfolio-item">
                <div className="item-wrap">
                    <img alt="" src="images/portfolio/dandeli.jpg" />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Dandeli</h5>
                        <p>Dandeli Wildlife Sanctuary, with its trails and dense forests, is home to animals including black panthers, monkeys and elephants, as well as many bird species.</p>
                      </div>
                    </div>
                
                </div>
              </div> {/* item end */}
              <div className="columns portfolio-item">
                <div className="item-wrap">
                    <img alt="" src="images/portfolio/gokak.jpg" />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Gokak</h5>
                        <p> The waterfall is horse shoe shaped at the crest, with a flood breadth of 177 metres (581 ft). </p>
                      </div>
                    </div>
                  
                </div>
              </div> {/* item end */}
              <div className="columns portfolio-item">
                <div className="item-wrap">
                    <img alt="" src="images/portfolio/kolhapur.jpg" />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Kolhapur</h5>
                        <p>Known for its temples, like the ancient Mahalakshmi Temple, a Hindu pilgrimage site.</p>
                      </div>
                    </div>
                  
                </div>
              </div> {/* item end */}
              <div className="columns portfolio-item">
                <div className="item-wrap">
                    <img alt="" src="images/portfolio/thoseghar.jpg" />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Thoseghar</h5>
                        <p>Thoseghar waterfalls is a scenic spot, series of waterfalls, some of them 15 to 20 metres and one of approx 200 metres in height.</p>
                      </div>
                    </div>
                
                </div>
              </div>  {/* item end */}
            </div> {/* portfolio-wrapper end */}
          </div> {/* twelve columns end */}
        </div> {/* row End */}
      </section>
      </React.Fragment>
    );
  }
}