import React, { Component } from 'react';
export default class Testimonials extends Component {
  render() {
    return (
      <React.Fragment>
      <section id="testimonials">
        <div className="text-container">
          <div className="row">
            <div className="two columns header-col">
              <h1><span>Blogs</span></h1>
            </div>
            <div className="ten columns flex-container">
              <div className="flexslider">
                <ul className="slides">
                  <li>
                    <blockquote>
                      <p>
                        ‘Traveling leaves you speechless, then turns you into a storyteller.’<br/>
                        I didn’t know what this truly meant until I set foot in Ooty! And since it left me with a childish longing to stay here forever, forgetting all about the world and work, I decided to come back with a bucket full of Ooty travel tips to let you in on the blissful experience…through my eyes! Here’s everything I experienced on my trip right from transport, food, what to do, and what not to do, down to the most offbeat experiences I had even in the peak season!
                      </p>
                      <cite>Shiv</cite>
                    </blockquote>
                  </li> {/* slide ends */}
                  <li>
                    <blockquote>
                      <p>Imagicaa is so much more than a collection of heart-thumping roller coasters, amazing themed attractions with animatronics, special effects, and entertaining live performances. We've also got some great food on offer at our 5 restaurants, featuring cuisine from all over India and the world.
                        We recently partnered with Seatrr, a food discovery platform that highlights the best a restaurant has to offer, and invited a select few food bloggers to come experience Imagicaa. In between visits to Nitro and Salimgarh, the group had a chance to satiate their taste buds at Robertos, Red Bonnet Diner, ZeZe, Arrmada, and Imagicaa Capital.
                      </p>
                      <cite>Shiv</cite>
                    </blockquote>
                  </li> {/* slide ends */}
                </ul>
              </div> {/* div.flexslider ends */}
            </div> {/* div.flex-container ends */}
          </div> {/* row ends */}
        </div>  {/* text-container ends */}
      </section>
      </React.Fragment>
    );
  }
}